.errorSection {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 100px;
  min-height: 50vh;
  h1 {
    font-size: 250px;
    font-family: "Roboto";
    font-weight: 800;
    color: rgb(187, 182, 182);
    margin: 30px;
    letter-spacing: 10px;
    padding-left: 10px;

    @media screen and (max-width: 500px) {
      font-size: 150px;
    }
  }
  a {
    font-size: 30px;
    text-decoration: none;
    &:hover {
      text-decoration: underline;
    }
    @media screen and (max-width: 500px) {
      font-size: 20px;
    }
  }
}
